const jwtDecode = require('jwt-decode');

var AuthPlugin = {
    setToken: function (token, expiration) {
        localStorage.setItem('authToken', token);
        localStorage.setItem('authTokenExpiration', expiration);
    },

    destroyToken: function () {
        localStorage.removeItem('authToken');
        localStorage.removeItem('authTokenExpiration');
    },

    getToken: function () {
        var token = localStorage.getItem('authToken');
        var expiration = localStorage.getItem('authTokenExpiration');

        if (!token || !expiration)
            return null;
        if (Date.now() / 1000 > parseInt(expiration)) {
            this.destroyToken();
            return null;
        } else {
            return token;
        }
    },

    loggedIn: function () {
		var token = this.getToken();

		if (token) {
			var decodedJWT = jwtDecode(token);
			if (!decodedJWT.stillNeedToTwoFA)
				return true;
		}
        return false;
    },
};

export default function(Vue) {
    Vue.auth = AuthPlugin;

    Object.defineProperties(Vue.prototype, {
        $auth: {
            get: function() {
                return Vue.auth;
            }
        }
    })
}



